<template>
  <div ref="wrapper" class="wrapper">
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'

  export default {
    props: {
      probeType: {        // 1
        type: Number,
        default: 1
      },
      click: {           // fastclickclass="needsclick"
        type: Boolean,
        default: true
      },
      listenScroll: {   // 
        type: Boolean,
        default: false
      },
      data: {
        type: [Number,Array],    // 
        default: null
      },
      pullup: {         // 
        type: Boolean,
        default: false
      },
      beforeScroll: {   // 
        type: Boolean,
        default: false
      },
      refreshDelay: {   // scroll
        type: Number,
        default: 20
      }
    },
    mounted() {
      setTimeout(() => {      // 20domready
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {    // dom
          return
        }else{
            this.scroll = new BScroll(this.$refs.wrapper, {   // mountedbetter-scroll
            probeType: this.probeType,
            click: this.click
          })
        }
        

        if (this.listenScroll) {      // 
          let me = this
          this.scroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }

        if (this.pullup) {
          this.scroll.on('scrollEnd', () => {       // 
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd')
            }
          })
        }

        if (this.beforeScroll) {        // 
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      disable() {     // 
        this.scroll && this.scroll.disable()
      },
      enable() {      // 
        this.scroll && this.scroll.enable()
      },
      refresh() { 
         // 
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {    // 
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {   //  dom
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    watch: {
      data() {  // 
        setTimeout(() => {
          this.scroll.refresh();
          
        }, this.refreshDelay)
      }
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  .wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



